<template>
  <div class="home">
    <div class="homebox">
      <div class="hometop">
        <div class="hometoptext-left fl">
          <strong>12</strong>
          <span>NOBEMBER</span>
        </div>
        <div class="hometoptext-right fr">
          <div class="search">
            <van-cell class="search-box" @click="showPopup"></van-cell>
            <van-popup
              position="top"
              round
              v-model="searchshow"
              close-icon-position="bottom-right"
              :style="{ height: '40%' }"
            >
              <van-search
                v-model="searchvalue"
                show-action
                shape="round"
                label
                input-align="center"
                placeholder="请输入搜索关键词"
              >
                <template #action>
                  <div @click="onSearch">搜索</div>
                </template>
              </van-search>
              <div class="searchdata">
                <div
                  class="searchitem flexbetween"
                  v-for="(item,index) in searchDatacom"
                  :key="index"
                  @click="getpid(item.pid)"
                >
                  <div class="imgandname flexbetween">
                    <div class="imgbox">
                      <img :src="item.smallImg" alt />
                    </div>
                    <div class="namebox">
                      <strong>{{item.name}}</strong>
                    </div>
                  </div>
                  <div class="Txt">
                    <span>￥{{item.price}}</span>
                  </div>
                </div>
              </div>
            </van-popup>
          </div>
          <div class="dz"></div>
        </div>
      </div>
      <div class="slogan">
        <span class="timetextbox">{{timetext}}好！</span>
        <span class="nametext">{{username}}</span>
      </div>
      <div class="homebody">
        <div class="bannerimg-box">
          <van-swipe class="my-swipe" :autoplay="3000" height="200" indicator-color="white">
            <van-swipe-item v-for="(item,index) in bannerData" :key="index">
              <img class="auto-img" @click="getpid(item.pid)" :src="item.bannerImg" alt />
              <div class="sm">{{item.name}}</div>
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="ishot">
          <div class="ishot-text">为你推荐</div>
          <div class="ishot-body clearfix">
            <div
              class="ishot-box fl"
              v-for="(item,index) in ishotData"
              :key="index"
              @click="getpid(item.pid)"
            >
              <div class="ishot-imgbox fl">
                <van-image width="80" height="80" round :src="item.smallImg" />
              </div>
              <div class="ishot-box-text fl">
                <strong>{{item.name}}</strong>
                <p>
                  <span>￥</span>
                  <span>{{item.price}}</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";
export default {
  name: "Home",
  created() {
    // 获取设置banner
    this.getbannerData();
    // 设置时间段
    this.settimeText();
    // 获取设置ishot
    this.getishotData();
    //获取个人信息
    this.getUserInfo();
  },
  data() {
    return {
      //名字
      username: "",
      searchshow: false,
      // 搜索词
      searchvalue: "",
      //搜索数据
      searchDatacom: [],
      bannerData: [],
      // 时间段显示
      timetext: "",
      //ishot
      ishotData: [],
    };
  },
  methods: {
    showPopup() {
      this.searchshow = !this.searchshow;
    },
    // 获取banner数据
    getbannerData() {
      this.$toast.loading({
        message: "加载中",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "get",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((retule) => {});
    },
    // 设置当前时间段
    settimeText() {
      let newtime = new Date().getHours();
      // 调用判断时间段函数
      this.showtimeText(newtime);
    },
    // 判断时间段函数
    showtimeText(time) {
      if (time > 3 && time <= 6) {
        this.timetext = "凌晨";
      } else if (time > 6 && time <= 8) {
        this.timetext = "早晨";
      } else if (time > 8 && time <= 11) {
        this.timetext = "上午";
      } else if (time > 11 && time <= 13) {
        this.timetext = "中午";
      } else if (time > 13 && time <= 17) {
        this.timetext = "下午";
      } else if (time > 17 && time <= 19) {
        this.timetext = "傍晚";
      } else if (time > 19 && time <= 23) {
        this.timetext = "晚上";
      } else if (time > 23 && time <= 3) {
        this.timetext = "深夜";
      }
    },
    //获取热门数据
    getishotData() {
      this.$toast.loading({
        message: "加载中",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 500) {
            this.ishotData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    // 获取并传送pid
    getpid(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //获取搜索数据
    onSearch() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          name: this.searchvalue,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          this.searchDatacom = [];
          if (result.data.code == "Q001") {
            if (result.data.result.length > 0) {
              this.$toast(result.data.msg);
              this.searchDatacom = result.data.result;
            } else {
              this.$toast("暂无此商品");
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //获取个人资料
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.isimgbool = false;
        // return this.$router.push("/login");
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          
          this.$toast.clear();
          if (result.data.code == "B001") {
            this.username = result.data.result[0].nickName;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

